
.obshi{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    position: relative;
}
.cardds img{
    border-radius: 6px;
    object-fit: contain;
    width: 100%;
    height: 200px;
}
.text{
    text-align: center;
}
.cardds{
    -webkit-box-shadow: 0px 0px 7px 4px rgba(100, 117, 129, 0.21);
    -moz-box-shadow: 0px 0px 7px 4px rgba(100, 117, 129, 0.21);
    box-shadow: 0px 0px 7px 4px rgba(100, 117, 129, 0.21);

    height: 300px;
    width: 100%;
    border-radius: 6px;
}
input{
    margin: 50px 0;
    width: 300px;
    padding: 10px 0;
    outline: none;
    border-radius: 6px;
    border: 3px solid black;
}
